import echo from '../libs/echo';

function lazyImg(){
    echo.init({
        offset : 100,//
        throttle : 0 //
    });
}

//
function safeUser(_this){
    _this.$store.dispatch("user/safeUser",{success:(res)=>{
            if(res.code!==200){
                _this.$router.replace('/login');
            }
        }});
}

export default {
    lazyImg,
    safeUser
}